import { useStaticQuery, graphql } from "gatsby"

export const useShopProducts = () => {
  const { allMdx } = useStaticQuery(
    graphql`
      query shopProducts {
        allMdx(
          filter: { slug: { regex: "/shop//" } }
          sort: { fields: id, order: ASC }
        ) {
          edges {
            node {
              id
              slug
              excerpt
              body
              frontmatter {
                title
                categories
                slug
                tags
                extra {
                  amount
                  link
                  mail
                  phone
                  price
                  vehicles {
                    premium
                    x
                  }
                }
              }
            }
          }
        }
      }
    `
  )
  return allMdx.edges
}
