/** @jsx jsx */
import React from "react"
import { Link, StaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"
import { jsx, useThemeUI } from "theme-ui"
import Layout from "Project/_components/layouts/001"
import PageCover from "Project/_components/pageCover"
import ProductsList from "Project/_components/productsList"
import SpecialOffers from "Project/_components/specialOffers"
// specialOffers

export default function IndexPage() {
  return (
    <Layout>
      <PageCover text={"Аккаунты World of Tanks"}>
        <StaticQuery
          query={graphql`
            {
              file(relativePath: { eq: "_assets/covers/cover-001.jpg" }) {
                childImageSharp {
                  fixed(width: 1600) {
                    ...GatsbyImageSharpFixed_withWebp
                  }
                }
              }
            }
          `}
          render={data => (
            <Img
              fixed={data.file.childImageSharp.fixed}
              style={{
                display: "flex",
                minHeight: "240px",
                width: "100%",
                position: "absolute !important",
              }}
            />
          )}
        ></StaticQuery>
      </PageCover>
      <SpecialOffers></SpecialOffers>
      <ProductsList
        title={"Рандомные аккаунты"}
        category={"randomAccount"}
      ></ProductsList>
      <ProductsList title={"Бонус коды"} category={"bonusCode"}></ProductsList>
    </Layout>
  )
}
