/** @jsx jsx */
import React from "react"
import { jsx, useThemeUI } from "theme-ui"

export default function PageCover(props) {
  const context = useThemeUI()
  const { theme } = context

  return (
    <div
      sx={{
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        alignItems: "center",
        height: "240px",
        userSelect: "none",
        marginBottom: "16px",
        position: "relative",
        ":after": {
          position: "absolute",
          left: "0",
          right: "0",
          top: "0",
          bottom: "0",
          content: `""`,
          backgroundImage:
            "linear-gradient(to bottom,transparent 0%,rgba(0,0,0,.2) 35%,rgba(0,0,0,.77) 100%)",
        },
        h1: {
          color: "white",
          textAlign: "center",
          margin: "0 !important",
        },
      }}
    >
      <div
        sx={{
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          paddingX: "16px",
          paddingY: "16px",
          backgroundColor: "rgba(0, 0, 0, 0.24)",
          backdropFilter: "blur(32px)",
          position: "absolute",
          zIndex: "1",
          borderRadius: `${theme.root.secondaryBorderRadius}`,
          "@media (max-width: 576px)": {
            width: "100%",
            height: "100%",
            borderRadius: "0px",
          },
        }}
      >
        <h1>{props.text}</h1>
      </div>
      {props.children}
    </div>
  )
}
