/** @jsx jsx */
import React from "react"
import { Link, StaticQuery, graphql } from "gatsby"
import { jsx, useThemeUI } from "theme-ui"
import Img from "gatsby-image"

export default function ProductList(props) {
  const context = useThemeUI()
  const { theme } = context

  return (
    <div
      sx={{
        userSelect: "none",
        marginBottom: "16px",
        position: "relative",
        margin: "16px",
      }}
    >
      <StaticQuery
        query={graphql`
          {
            file(relativePath: { eq: "_assets/special-offers/001.jpg" }) {
              childImageSharp {
                fixed(width: 300) {
                  ...GatsbyImageSharpFixed_withWebp
                }
              }
            }
          }
        `}
        render={data => (
          <>
            <Link
              to={"/offer-001"}
              sx={{ display: "flex", minHeight: "60px", maxWidth: "300px" }}
            >
              <Img
                fixed={data.file.childImageSharp.fixed}
                // style={{
                //   display: "flex",
                //   minHeight: "60px",
                //   maxWidth: "300px",
                // }}
              />
            </Link>
          </>
        )}
      ></StaticQuery>
    </div>
  )
}
