/** @jsx jsx */
import React from "react"
import { Link } from "gatsby"
import { jsx, useThemeUI } from "theme-ui"
import { useShopProducts } from "Project/_static-queries/shop-products"
import BuyModal from "Project/_components/buyModal"

export default function ProductList(props) {
  const context = useThemeUI()
  const { theme } = context
  const shopProductsData = useShopProducts()

  return (
    <div className={"product-list"} sx={{ padding: "16px" }}>
      <h2
        sx={{
          marginBottom: "16px",
          "@media (max-width: 768px)": {
            paddingX: "16px",
          },
        }}
      >
        {props.title}
      </h2>
      <div
        className="table-responsive"
        sx={{
          table: {
            thead: { userSelect: "none" },
            tbody: {
              boxShadow: `inset 1px 0px 0px ${theme.colors.separator_opaque},inset -1px 0px 0px ${theme.colors.separator_opaque},inset 0px 1px 0px ${theme.colors.separator_opaque},inset 0px -1px 0px ${theme.colors.separator_opaque}`,
              tr: {
                ":nth-of-type(odd)": {
                  backgroundColor: "fill_quarternary",
                },
              },
            },
            th: {
              paddingX: "16px",
              color: "text_primary",
              textTransform: "uppercase",
              display: "table-cell",
              fontWeight: "normal",
              borderBottomWidth: "0px",
              minHeight: "32px",
              whiteSpace: "nowrap",
            },
            td: {
              paddingX: "16px",
              display: "table-cell",
              color: "text_tertiary",
              borderBottomWidth: "0px",
            },
            tr: {
              ":hover": {
                ".table-buy-button": {
                  color: "white",
                  backgroundColor: "tint_blue",
                },
              },
            },
          },
          "@media (max-width: 767.98px)": {
            scrollbarWidth: "none",
            msOverflowStyle: "none",
            "::-webkit-scrollbar": {
              display: "none",
            },
            table: {
              width: "768px",
              tbody: {
                tr: {
                  ".table-buy-button": {
                    color: "white",
                    backgroundColor: "tint_blue",
                  },
                },
              },
            },
          },
        }}
      >
        <table className="table">
          <thead>
            <tr>
              <th className={"element-small"} scope="col">
                Товар
              </th>
              <th className={"element-small"} scope="col">
                Наличие
              </th>
              <th className={"element-small"} colSpan="2">
                Цена за 1 шт.
              </th>
            </tr>
          </thead>
          <tbody>
            {shopProductsData.map(
              ({ node }) =>
                node.frontmatter.categories.indexOf(props.category) !== -1 && (
                  <tr key={node.id}>
                    <td className={"element-medium"} scope="col">
                      {node.frontmatter.extra.link !== null && (
                        <a
                          href={node.frontmatter.extra.link}
                          target={"blank"}
                          sx={{ color: "tint_blue", marginRight: "8px" }}
                        >
                          Ссылка на аккаунт.
                        </a>
                      )}
                      <Link to={node.slug} sx={{ color: "text_tertiary" }}>
                        {node.excerpt}
                      </Link>
                    </td>
                    <td className={"element-medium"} scope="col">
                      {node.frontmatter.extra.amount}
                    </td>
                    <td
                      className={"element-medium"}
                      scope="col"
                      sx={{
                        paddingRight: "0px !important",
                        paddingY: "0px !important",
                      }}
                    >
                      <div
                        sx={{
                          display: "grid",
                          gridAutoFlow: "column",
                          justifyContent: "space-between",
                          alignItems: "center",
                        }}
                      >
                        <span
                          sx={{ whiteSpace: "nowrap" }}
                        >{`${node.frontmatter.extra.price} ₽`}</span>
                        <button
                          data-toggle="modal"
                          data-target={`#${node.id.replace(
                            /[.,"*+!-?^${}()|[\]\\/]/gu,
                            ""
                          )}`}
                          className={
                            "element-medium element-button table-buy-button"
                          }
                          sx={{
                            color: "tint_blue",
                            marginLeft: "8px",
                            paddingX: "16px",
                            borderRadius: `0px !important`,
                          }}
                        >
                          Купить
                        </button>
                      </div>
                      <BuyModal data={node}></BuyModal>
                    </td>
                  </tr>
                )
            )}
          </tbody>
        </table>
      </div>
    </div>
  )
}
